
a {
  color: #9b64bc;
}
.russian-green {
  color: #6d9f71;
}
.red-text {
  color: #d00d2d;
}
::-moz-selection {
  color: white;
  background: #766C7F; /* WebKit/Blink Browsers */
}
::selection {
  color: white;
  background: #766C7F; /* WebKit/Blink Browsers */
}
::-moz-selection {
  color: white;
  background: #766C7F; /* Gecko Browsers */
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 0.25s ease-out;
  transition: opacity 0.25s ease-out;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.pb-3 {
  padding-bottom: 3rem;
}
.pt-1-half {
  padding-top: 1.5rem;
}
/* width */
::-webkit-scrollbar {
  width: .5rem;
}
/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #766C7F; /* WebKit/Blink Browsers */
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #888;
}
.columnst {
  margin: 0;
}
@media screen and (min-width: 768px) {
.columns {
    margin-left: -0.75rem;
    margin-right: -0.75rem;
    margin-top: -0.75rem;
}
}
